/* Style for the footer container */
.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #04060D;
    color: #fff;
    height: 40px; /* Match the height of the footer */
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    z-index: 1; /* Ensure it's above other elements */
    padding: 0 10px; /* Optional padding */
  }
  
  /* Container for the flagger */
  .flag-container {
    flex: 1;
    overflow: hidden;
    position: relative;
    white-space: nowrap;
  }
  
  /* Flagging animation */
  .flagger {
    display: inline-block;
    padding: 10px;
    background-color: #f0f0f0;
    color: #ffffff;
    font-size: 16px;
    font-weight: bold;
    border-radius: 5px;
    animation: slide 5ms; /* Adjust duration as needed */
  }
  
  /* Keyframes for the animation */
  @keyframes slide {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  