/* Center the login form on the page */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 2rem;
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: white;
  overflow: hidden;
  border-radius: 10rem;
}

.login-container::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url('../assets/aqslogo.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  opacity: 0.3;
  width: 80%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

.login_heading {
  padding-bottom: 1rem;
  text-align: center;
  z-index: 2;
}

.link-style {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  font-size: larger;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  z-index: 2;
}

.login-form {
  
  /* background: rgb(154, 188, 252); */
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
  width: 300px;
  text-align: center;
  z-index: 1;
  position: relative;
}

.login-image{
  display: block;
  border-radius: 1.5rem;
  margin: 0 auto 20%;
  width: 50px;
  height: 50px;
}
.login-form h1 {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
  text-align: left;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.form-group input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.login-button {
  width: 100%;
  padding: 10px;
  background-color: #06648a;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.login-button:hover {
  background-color: #013950;
  
}
  