body {
  font-family: 'Times New Roman', Times, serif;
  align-items: center;
  padding-left: 0%;
  /* background-color: white;
  border: none; */
}

.watermark-form {
  position: relative;
  background-image: url('../assets/aqslogo.jpg'); /* Replace with your logo path */
  background-size: 150px; /* Adjust the size of the watermark */
  background-position: center; /* Center the watermark */
  background-repeat: no-repeat;
  opacity: 0.2; /* Adjust the opacity to make it look like a watermark */
}

.aqs_logo {
  width: 50px;
  height: 1px;
}

.card-header{
  background-color: rgb(207, 207, 207);
  padding-top: 10px;
  height: 5rem;
}

.container {
  margin-top: 1rem;
  padding-left: 150px;
  /* background-color: #d3d9d4; */
  margin-bottom: 5rem;
}

.read-only-field {
  background-color: #e7e7e7;
}

.card {
  width: 78rem;
}

.text_fields {
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  font-size: 20px;
  /* font-weight: bold; */
}

.custom-border {
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  padding: 8px;
}

.wide-column {
  width: 5rem; /* Adjust this value to set the desired width */
}

.file {
  align-items: center;
  margin-left: 10rem;
  justify-content: center;
}

.buttoms {
  margin-top: 2rem;
  align-content: center;
  justify-content: space-between;
  margin-left: 30rem;
}

.buttoms .btn:hover {
  background-color: #124e66;
}

.row .card .card_cody .table {
  background-color: #d3d9d4;
}

.table{
  text-align: center;
  align-content: center;
  justify-content: space-between;
  border: 1px solid rgb(204, 204, 204);
}

.main_container {
  margin-top: 1rem;
  padding-left: 200px;
  box-sizing: border-box;
  flex: auto;
  justify-content: center;
}

.menu_buttons .btn {
  height: 9rem;
  width: 8rem;
  font-weight: bold;
  background-color: rgb(149, 201, 201);
  /* box-shadow: 0 10px 8px rgba(19, 9, 34, 0.3); */
  margin-right: 2rem;
  border: none;
  transition: all 0.3s ease;
  margin-top: 2rem;
  box-shadow: 20px 20px 20px rgb(85, 85, 85);
}

.menu_buttons .btn:hover {
  background-color: rgb(34, 73, 73);
  color: rgb(255, 255, 255);
}

img:hover{
  color: white;
  /* align-content: center; */
}

img{
  width: 50px;
  height: 50px;
  border-radius: 0.5rem;
  /* align-content: center; */
}

img .gauge{
  width: 50px;
  height: 50px;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 225px;
  justify-content: space-between;
  background-color:  #192841;
  color: white;
  padding-top: 0px;
}

.logo {
  width: 80px;
  height: 70px;
  align-items: center;
  justify-content: center;
  opacity: inherit;
  margin-top: 1.5rem;
  margin-left: 4rem;
  margin-bottom: 1.5rem;
}

.sidebar-header {
  padding: 10px 15px;
  background-color: #555;
}

.sidebar-brand {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
}

.sidebar ul.components {
  padding: 0;
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.nav-link {
  display: block;
  padding: 10px 15px;
  color: #fff;
  text-decoration: none;
  position: relative;
}

.nav-link:hover {
  background-color: #575757;
}

.nav-link.active {
  background-color: #666;
}

.dropdown {
  position: relative;
}

.dropdown-menu {
  width: 5rem;
  display: block;
  position: absolute;
  left: 0;
  top: 100%;
  background-color: #969696;
  padding: 0;
  margin: 0;
  list-style: none;
  z-index: 1000;
  text-decoration: none;
}

.dropdown-item {
  padding: 5px 10px;
  display: block;
  color: black;
  text-decoration: none;
}

.dropdown-menu li {
  padding: 10px 15px;
  white-space: nowrap;
  width: 100%;
}

.dropdown-menu li a{
  text-decoration: none;
  color: white;
}

.dropdown-menu li:hover {
  background-color: #575757;
  width: 10rem;
}

.dropdown-item:hover {
  background-color: #f8f9fa;
  color: black;
}

.search-export-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.search-export-container .form-control {
  margin-right: 10px;
}

.card-header{
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif;
  text-decoration: underline;
  color: #292829;
}

.menu-header{
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif;
  text-decoration: underline;
  color: #000000;
  margin-top: 1rem;
}

.table_heading th{
  background-color: #192841;
  color: #dbdbdb;
  font-size: 16px;
}

.add_button{
  background-color:  rgb(13, 114, 114);
  color: #ffffff;
}

.add_button:hover{
  background-color:   rgb(13, 114, 114);
  color: #ebebeb;
}

.edit_button{
  background-color:   rgb(13, 114, 114);
  color: #ffffff;
}

.edit_button:hover{
  background-color:  rgb(84, 175, 175);
  color: #ebebeb;
}

.sch_btn{
  background-color: rgb(13, 114, 114);
  color: #ffffff;
}

.sch_btn:hover{
  background-color: rgb(13, 114, 114);
  color: #ffffff;
}

.save_button{
  background-color:   rgb(13, 114, 114);
