.btn {
  padding-top: 0.5rem;
  align-items: center;
  font-weight: bold;
  align-content: center;
}

.export-button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0.6rem 0.5rem #282337;
  border-radius: 5px;
}

.export-button {
  padding: 10px 20px;
  background-color: #dfdfdf; /* Green */
  border: none;
  color: rgb(0, 0, 0);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}

.export-button:active {
  transform: translateY(0.1rem);
  box-shadow: 0 0.5rem 0.3rem #282337;
}

.observation_table{
  justify-content: space-between;
  align-content: center;
  padding-left: 10px;
  padding-right: 10px;
}

.observation_table .table{
  border: 1px solid rgb(156, 156, 156);
  border-radius: 10px;
  
}

.observation_table .table th{
  
  background-color: #192841 ;
  color: white;
  font-size: 15px;
  height: 50px;
}

.observation_table .table td{
  
  background-color: #d8d8d8 ;
  color: #101929 ;
  font-size: 15px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.no-wrap {
  white-space: nowrap;
  text-align: center;
}

.approval-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 5px 5px;
  border: none;
  border-radius: 4px;
  background-color: #0d7272;
  color: #fff;
  cursor: pointer;
  margin-right: 10px;
}

button:hover {
  background-color: #0b5a5a;
}
